@charset "utf-8";
*{margin:0px;padding:0px;}
ul,li,ol{list-style: none;}
body{
    font-family: "맑은 고딕",arial,sans-serif;
    font-size:12px;
    color:#333;
}
.bg{
    position:absolute;
    top:0px;left:0px;
    z-index: 100;
    width:600px;
    /* margin:0px auto; */
}
.box{
    margin:0px auto;
    width:500px;
    position:relative;
}
.wrap{
    width:500px;
    height:971px;
    padding:100px 50px;
    margin:0px auto;
    overflow: hidden;
    /* position:relative; */
    border-radius: 90px;
}
.wrap h2{
    text-align: center;
}
.wrap h1{
    text-align: center;
    padding:10px 0px;
}
.form{
    width:500px;
    height:280px;
    /* background:#ccc; */
    margin:25px auto;
    position:relative;
    z-index: 102;
}
.form p{
    text-align: center;
    font-weight: bold;
}
.form input[type=text]{
    width:260px;
    height:28px;
    margin:10px 20px;
    font-size:12px;
}
.form #product_name{
    margin-left:30px;
}
.form input[type=button]{
    width:120px;
    height:30px;
    margin-top:10px;
    transform: translateX(-45px);
    background:rgb(0, 196, 130);
    border:none;
    color:#fff;
}
.product_list{
    display:flex;
    flex-wrap:wrap;
    width:100%;
    float:left;
    margin:0px 1.6% 30px 0px;
    padding-bottom:10px;
    font-size: 14px;
    border-bottom:1px solid #000;
    z-index: 100;
    position:relative;
}
.product_list >li{
    width:100%;
}
.product_list .txt_box{
    width:260px;
    padding:4px 16px;
    float:right;
}
.product_list .txt_box p{
    margin-bottom:14px;
}
.product_list img{
    width:200px;
}
.product_list .txt_box input{
    width:100px;
    height:30px;
    float:right;
    background:rgb(0, 196, 130);
    border:none;
    color:#fff;
    position:relative;
    z-index:103;
}

